@colorWhite:white;
@colorBlue:#09d;

.firstNav .navbar-brand .glyphicon {
  font-size: 24px;
  font-style: italic;
  color: @colorWhite;
}
.firstNav .navbar-collapse .nav a{
  color: @colorWhite;
}
.navbar{
  margin-bottom: 0;
}

.secondNav .navbar-left{
  height: 80px;
  line-height: 80px;
  padding: 0px 15px;
  font-size: 16px;
}
.secondNav .navbar-brand img{
  margin-top: 11px;
  margin-right: 30px;
}
.secondNav .navbar-left li:hover{
  background: #f1f1f1;
}
.secondNav .navbar-left li a:hover{
  color: @colorBlue;
}
.secondNav .navbar-left li a{
  height: 80px;
  width: 100px;
  line-height: 50px;
  text-align: center;
}
.feature01{
  background: url("../../images/feature01.png") no-repeat;
}
.feature02{
  background: url("../../images/feature02.png") no-repeat;
}
.feature03{
  background: url("../../images/feature03.png") no-repeat;
}
.featureAll>div{
  padding-left: 80px;
  height: 66px;
}
.featureAll{
  margin-top: 10px;
}
.featureAll>div>dt{
  font-size: 18px;
  margin: 10px 0;
}